'use client';

import { useEffect, useRef } from 'react';
import { DashboardLayout } from '@/components/dashboard/dashboard-layout';
import { DashboardHeader } from "@/components/dashboard/dashboard-header";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { MapPin, Navigation, List, Loader2 } from "lucide-react";
import { Card } from "@/components/ui/card";
import ProtectedRoute from "@/components/ProtectedRoute";
import { useSearchParams, useRouter } from "next/navigation";
import { useToast } from "@/components/ui/use-toast";

// Import CSS but not the library (it will be loaded dynamically)
import 'leaflet/dist/leaflet.css';

// Very simple map page that focuses on getting the map to display
export default function MapPage() {
  const mapContainerRef = useRef(null);
  const mapInstanceRef = useRef(null);
  const { toast } = useToast();
  const router = useRouter();
  const searchParams = useSearchParams();

  // Initialize map when component mounts
  useEffect(() => {
    if (typeof window === 'undefined' || !mapContainerRef.current || mapInstanceRef.current) {
      return;
    }

    const initializeMap = async () => {
      try {
        // Dynamically import Leaflet
        const L = await import('leaflet');
        
        // Clear any previous map
        if (mapContainerRef.current) {
          mapContainerRef.current.innerHTML = '';
        }
        
        // Fix Leaflet icon issues
        delete L.Icon.Default.prototype._getIconUrl;
        L.Icon.Default.mergeOptions({
          iconRetinaUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon-2x.png',
          iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png',
          shadowUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-shadow.png',
        });
        
        // Default NYC coordinates
        const defaultCenter = [40.7128, -74.0060];
        
        // Create the map with explicit height
        const map = L.map(mapContainerRef.current, {
          center: defaultCenter,
          zoom: 13,
        });
        
        // Add OpenStreetMap tiles
        L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          maxZoom: 19,
          attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
        }).addTo(map);
        
        // Add a marker at the center
        L.marker(defaultCenter).addTo(map)
          .bindPopup('New York City')
          .openPopup();
        
        // Store map reference
        mapInstanceRef.current = map;
        
        // Force a redraw after a short delay
        setTimeout(() => {
          if (map) {
            map.invalidateSize();
          }
        }, 100);
        
        // Add location detection
        map.on('click', function(e) {
          const { lat, lng } = e.latlng;
          L.marker([lat, lng]).addTo(map)
            .bindPopup(`You clicked at: ${lat.toFixed(4)}, ${lng.toFixed(4)}`)
            .openPopup();
            
          toast({
            title: "Location selected",
            description: `${lat.toFixed(4)}, ${lng.toFixed(4)}`,
          });
        });
        
      } catch (error) {
        console.error("Error initializing map:", error);
        toast({
          variant: "destructive",
          title: "Map Error",
          description: "Failed to initialize the map. Please try again.",
        });
      }
    };

    // Initialize the map
    initializeMap();

    // Cleanup
    return () => {
      if (mapInstanceRef.current) {
        mapInstanceRef.current.remove();
        mapInstanceRef.current = null;
      }
    };
  }, [toast]);

  return (
    <ProtectedRoute allowedRoles={['student']}>
      <DashboardLayout>
        <div className="flex flex-col gap-4">
          <DashboardHeader 
            heading="Events Near Me" 
            text="Discover events happening in your area" 
          />
          
          <div className="flex gap-2 mb-4">
            <Button size="sm" className="gap-1">
              <Navigation className="h-4 w-4" />
              Update Location
            </Button>
            
            <Button variant="outline" size="sm">
              <List className="h-4 w-4 mr-1" />
              List View
            </Button>
            
            <Badge variant="outline" className="self-start text-xs ml-auto">
              New York City
            </Badge>
          </div>
          
          {/* Map container with fixed dimensions */}
          <Card className="w-full relative border">
            <div 
              ref={mapContainerRef} 
              style={{ 
                height: "500px", 
                width: "100%", 
                zIndex: 0,
                position: "relative" 
              }} 
            />
          </Card>
          
          <p className="text-sm text-muted-foreground">
            Click anywhere on the map to mark a location
          </p>
        </div>
      </DashboardLayout>
    </ProtectedRoute>
  );
}